body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #a4afc1;
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: absolute;
  width:100%;
  height:100%;
}

.table {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content:center;
          justify-content:center;
  -webkit-align-content: center;
          align-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 600px;
  height: 550px;
  background-color: #5371a3;
  border-radius: 25px;
  margin-bottom: 15px;
}

.cells {
  line-height: 150px;
  text-align: center;
  font-size:90px;
  color:#d8b04b;
  width: 150px;
  height: 150px;
  border:5px solid #5982c6;
}

[data-index="8"], [data-index="7"], [data-index="6"] {
  border-bottom: none;
}

[data-index="0"], [data-index="1"], [data-index="2"] {
  border-top: none;
}

[data-index="0"], [data-index="3"], [data-index="6"] {
  border-left: none;
}

[data-index="2"], [data-index="5"], [data-index="8"] {
  border-right: none;
}

.bottom {
  border-bottom: none;
}

.button {
  width: 600px;
  height: 55px;
  border-radius: 25px;
  border:none;
  margin-bottom: 35px;
  background-color: #5371a3;
  font-size: 35px;
  font-weight:700;
  color: #d8b04b;
}

.button:hover {
  background-color: #5982c6;
}

.button:focus  {
  outline: none;
}

.info {
  font-size:45px;
  color:#d8b04b;
}

.table__last-td {
  border-bottom: none;
}

